html {
  height: 100%;
  background: -webkit-linear-gradient(233deg, rgba(255,0,0,.8), rgba(255,0,0,0) 70.71%),
            -webkit-linear-gradient(323deg, rgba(0,255,0,.8), rgba(0,255,0,0) 70.71%),
            -webkit-linear-gradient(114deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%);
  background: -o-linear-gradient(233deg, rgba(255,0,0,.8), rgba(255,0,0,0) 70.71%),
            -o-linear-gradient(323deg, rgba(0,255,0,.8), rgba(0,255,0,0) 70.71%),
            -o-linear-gradient(114deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%);
  background: linear-gradient(217deg, rgba(255,0,0,.8), rgba(255,0,0,0) 70.71%),
            linear-gradient(127deg, rgba(0,255,0,.8), rgba(0,255,0,0) 70.71%),
            linear-gradient(336deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%);
}

.headline {
  height: 100%;
  text-align: center;
  color: blue;
  font-size: 200px;
  font-family: Arial, Helvetica, sans-serif;
  background: transparent;
}


